import React, { Component } from 'react';
import { 
  Button,
  Card, 
  CardBody, 
  CardHeader, 
  Col, 
  Row, 
  Fade,
  Collapse,
  Input,
  FormGroup,
  Label
} from 'reactstrap';
import { history } from '../../helpers/history';
import { connect } from 'react-redux';
import { getDataById, editData } from '../../redux/dataUser/dataUserAction';

class Tables extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.toggleFade = this.toggleFade.bind(this);
        this.state = {
            collapse: true,
            fadeIn: true,
            timeout: 300,
            nama: '',
            email: '',
            username: '',
            password: '',
            image: '',
            link_image: '',
            data: [],
        };
        this.handleClick = this.handleClick.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
    }

    async componentWillMount(){
        const id = this.props.match.params.id;
        await this.props.dispatch(getDataById(id))
    }

    async componentWillReceiveProps(nextProps){
        const newData = await nextProps.datas.datas.datas;
        this.setState({
            data: newData,
            nama: newData ? newData[0].nama : null,
            email: newData ? newData[0].email : null,
            username: newData ? newData[0].username : null,
            password: newData ? newData[0].password : null,
            image: newData ? newData[0].image : null,
            link_image: newData ? newData[0].link_image : null
        })
    }

    handleClick = event => {
        event.preventDefault();
        const data = new FormData();
        data.append('nama', this.state.nama);
        data.append('email', this.state.email);
        data.append('username', this.state.username);
        data.append('password', this.state.password);
        data.append('image', this.state.image);
        data.append('id', this.props.match.params.id);

        const config = {
            headers: {
                'content-type': 'multipart/form-data'
            }
        }
        
        this.props.dispatch(editData(data, config));
    };

    handleChange = prop => event => {
        this.setState({ [prop]: event.target.value });
    }

    handleFileChange = prop => event => {
        this.setState({
          image: event.target.files[0]
        })
    }

    handleCancel() {
        history.push('/data-user');
    }

    toggle() {
      this.setState({ collapse: !this.state.collapse });
    }

    toggleFade() {
      this.setState((prevState) => { return { fadeIn: !prevState }});
    }
  
    render() {
        // console.log(this.state.data)
        return (
          <div className="animated fadeIn">
            <Row>
              <Col xs="12">
                <Fade timeout={this.state.timeout} in={this.state.fadeIn}>
                  <Card>
                    <CardHeader>
                      <i className="fa fa-edit"></i> Edit Data User
                      <div className="card-header-actions">
                        <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                        <Button color="link" className="card-header-action btn-minimize" data-target="#collapseExample" onClick={this.toggle}><i className="icon-arrow-up"></i></Button>
                      </div>
                    </CardHeader>
                    <Collapse isOpen={this.state.collapse} id="collapseExample">
                      <CardBody>
                        <form onSubmit={this.handleClick} noValidate>
                            <div className="row">
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <FormGroup>
                                      <Label for="exampleEmail">Nama</Label>
                                      <Input 
                                        type="text" 
                                        name="nama" 
                                        onChange={this.handleChange('nama')} 
                                        value={this.state.nama || ""}
                                        placeholder="Nama" />
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12">
                                    <FormGroup>
                                      <Label for="exampleEmail">Email</Label>
                                      <Input 
                                        type="email" 
                                        name="email" 
                                        onChange={this.handleChange('email')} 
                                        value={this.state.email || ""}
                                        placeholder="Email" />
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <FormGroup>
                                      <Label for="exampleEmail">Username</Label>
                                      <Input 
                                        type="text" 
                                        name="username" 
                                        onChange={this.handleChange('username')} 
                                        value={this.state.username || ""}
                                        placeholder="Username" />
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <FormGroup>
                                      <Label for="exampleEmail">Password</Label>
                                      <Input 
                                        type="password" 
                                        name="password" 
                                        onChange={this.handleChange('password')} 
                                        value={this.state.password || ""}
                                        placeholder="Password" />
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <FormGroup>
                                      <Label for="exampleFile">File Image</Label>
                                      <Input 
                                        type="file" 
                                        name="image" 
                                        onChange={this.handleFileChange()} />
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <img alt="" src={this.state.link_image} width={400} height={400} />
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12">
                                    <div style={{ float: 'right' }}>
                                        <button 
                                            type="submit" 
                                            className="btn btn-primary data-submit" 
                                        >
                                            Submit
                                        </button>
                                        &nbsp;
                                        <button
                                            className="btn btn-danger"
                                            onClick={this.handleCancel}
                                        >
                                            Cancel
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </form>
                      </CardBody>
                    </Collapse>
                  </Card>
                </Fade>
              </Col>
            </Row>
          </div>    
        );
    }
}

const mapStateToProps = (state) => {
    return {
        datas: state.dataUser
  };
}

// const mapDispatchToProps = (dispatch) => {
//   return {
//     actions: bindActionCreators(createData, dispatch)
//   };
// }

export default connect(mapStateToProps)(Tables);